<template>
  <form class="ui form segment" v-on:submit.preven>
    <h3 class="ui header"></h3>
    <div class="two fields">
      <div class="field">
        <label></label>
        <input type="text" v-model="name" placeholder="">
      </div>
       <div class="field">
        <label></label>
        <div class="ui dropdown selection">
          <input type="hidden">
          <div class="default text"></div>
          <i class="dropdown icon"></i>
          <div class="menu">
            <div class="item" data-value="default">Default</div>
            <div class="item" data-value="ssp">SSP</div>
            <div class="item" data-value="dc"></div>
          </div>
        </div>
      </div>
    </div>
    <div class="field">
      <label></label>
      <input type="text" v-model="cmd" placeholder="">
    </div>
    <div class="field">
      <div class="ui toggle checkbox">
        <input type="checkbox" class="hidden" v-bind:checked="!pause">
        <label>{{pause ? '' : ''}}</label>
      </div>
    </div>
    <button class="fluid blue ui button" type="submit"></button>
  </form>
</template>

<script>
export default {
  name: 'job-edit',
  data: function(){
      return {
        name: '',
        group: 'default',
        cmd: '',
        pause: false
      }
  },

  mounted: function(){
    var vm = this;
    $(this.$el).find('.checkbox').checkbox({
      onChange: function(){
        vm.pause = !vm.pause;
      }
    });

    $(this.$el).find('.dropdown').dropdown({
      allowAdditions: true,
      onChange: function(value, text, $choice){
        vm.group = value;
      }
    }).dropdown('set selected', this.group);
  }
}
</script>


